.cta {
    position: relative;
    height: 100vh;
    padding: 0;

    @include breakpoint(tablet) {
        height: 60vh;
    }

    @include breakpoint(laptop) {
        height: 100vh;
    }

    &::after {
        background: #3A3A3A;
        mix-blend-mode: multiply;
        opacity: 0.3;
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        content: '';
        z-index: 1;
    }

    .graphic {
        position: absolute;
        right: 0;
        height: 50%;
        z-index: 4;

        @include breakpoint(mobileonly) {
            display: none;
        }

        img {
            width: 100%;
            height: 100%;
        }

        &.top {
            top: 0;
        }

        &.bottom {
            bottom: 0;
        }
    }

    .cta-logo {
        position: absolute;
        left: -20px;
        top: 0;
        z-index: 4;

        .logo {
            position: absolute;
            left: 37%;
            top: 23%;
            transform: translate(-50%, -50%);
        }

        @include breakpoint(mobileonly) {
            width: 90%;

            .bg {
                img {
                    width: 100%;
                }
            }

            .logo {
                left: 30%;
            }
        }

    }

    .cta-video {
        width: 100%;
        height: 100%;

        video {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .cta-text {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        @include champagneText;
        @include h2;
        z-index: 5;
        text-align: center;

        @include breakpoint(untilTablet) {
            width: 100%;
        }
    }

    .cta-logo-2 {
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: 2;
        z-index: 5;

        .logo {
            position: absolute;
            left: 60%;
            top: 55%;
            transform: translate(-50%, -50%);
            display: flex;
            align-items: center;

            .text {
                margin-right: 22px;
                text-transform: uppercase;
                @include headButton;
                color: $lightgrey;
                white-space: nowrap;
            }
        }
    }
}

.about-grid {

    .item {
        align-items: center;
        height: 100vh;

        &:first-child {

            background-color: $lightgrey;
        }

        @include breakpoint(untilLaptop) {
            height: auto;
        }

        .item-text {
            padding: 100px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include breakpoint(largeDesktop) {
                padding: 200px;
            }

            @include breakpoint(untilTablet) {
                padding: 64px 24px;
            }
        }

        &.orange {
            position: relative;
            color: $lightgrey;

            .bg {
                position: absolute;
                right: 0;
                display: flex;
                justify-content: flex-end;
                top: 0;
                width: 100%;
                height: 100%;
                z-index: -1;

                img {
                    height: 100%;
                    width: 100%;

                    @include breakpoint(desktop) {
                        width: 50%;
                    }

                    object-fit: cover
                }
            }

            @include breakpoint(desktop) {
                .item-text {
                    height: 100%;
                }
            }

            .title {
                @include champagneText;
            }
        }

        &.blue {
            background-color: $blue;
            color: $lightgrey;

            .title {
                @include champagneText;
            }
        }
    }

    .title {
        @include h1;
        margin-bottom: 36px;
        color: $blue;
    }

    .subtitle {
        @include subtitle1;
        color: $primary;
        margin-bottom: 6px;
    }

    .text {
        @include body1;
        text-align: justify;
    }

    .item-img {
        padding: 0 !important;
        height: 100%;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: left;

        }

        @include breakpoint(untilLaptop) {
            height: 50vh;

            img {
                object-fit: cover;
                object-position: top;
            }
        }
    }
}


.units {
    position: relative;
    padding: 96px 0;

    .bg {
        @include bg;
    }

    .unit {
        align-items: center;
        position: relative;

        .ornament {
            position: absolute;
            top: 0;


        }

        &:last-child {
            margin-top: 75px;
        }

        .item-img {
            padding-left: 40px;

            img {
                width: 100%;
            }
        }

        .item-text {
            position: relative;

            .bg-border {
                @include bg;

                @include breakpoint(untilTablet) {
                    width: 95%;
                    left: 50%;
                    transform: translateX(-50%);
                }

                img {
                    object-fit: contain;
                }
            }

            .ornament-2 {
                position: absolute;
                bottom: 45px;
                right: 45px;
                z-index: 5;

                @include breakpoint(untilTablet) {
                    right: 0;
                }
            }

            .content {
                width: 80%;
                margin: auto;

                .top-section {
                    border: 3px solid $border-color;
                    border-radius: 20px 20px 0 0;
                    padding: 80px 30px 10px 55px;
                    width: 70%;
                    z-index: 3;
                    position: relative;
                    border-bottom: none;
                }

                .bottom-section {
                    padding: 60px;
                    padding-left: 55px;
                    border-radius: 0 20px 20px 20px;
                    border: 3px solid $border-color;
                    z-index: 2;
                    position: relative;
                    border-top: none;

                    &:after {
                        background-color: $border-color;
                        content: "";
                        height: 3px;
                        position: absolute;
                        right: 10px;
                        top: 0px;
                        width: 28%;
                    }
                }

                @include breakpoint(untilTablet) {
                    width: 90%;

                    .top-section {
                        padding: 60px 30px 10px 35px;
                        width: 80%;
                    }

                    .bottom-section {

                        &::after {
                            width: 17%;
                        }

                        padding: 40px;
                        padding-left: 35px;
                        padding-right: 35px;
                    }
                }
            }

            &.diponegoro {
                .bg-border {
                    @include breakpoint(untilTablet) {
                        display: none;
                    }

                    @include breakpoint(tablet) {
                        display: block;
                    }


                    &.mobile {
                        display: block;

                        @include breakpoint(tablet) {
                            display: none;
                        }
                    }
                }

                .ornament {
                    right: 20px;
                    left: auto;
                    top: auto;
                    bottom: 160px;
                    position: absolute;
                    z-index: 5;

                    @include breakpoint(untilLaptop) {
                        right: 45px;
                    }

                    @include breakpoint(mobileonly) {
                        right: 0;
                    }
                }

                .content {
                    padding-bottom: 180px;
                    width: 90%;
                    margin: auto;

                    .top-section {
                        width: 80%;
                    }

                    @include breakpoint(untilTablet) {
                        width: 95%;
                    }

                    .bottom-section {
                        border-bottom: none;
                        border-radius: 0 20px 0 0;

                        &::after {
                            width: 18%;
                        }

                        &::before {
                            content: '';
                            bottom: 0;
                            width: calc(100% - 152px);
                            left: 50%;
                            transform: translateX(-50%);
                            height: 3px;
                            background-color: $border-color;
                            position: absolute;
                        }

                        .bottom {
                            height: 80px;
                            width: 80px;
                            position: absolute;
                            left: -3px;
                            bottom: -80px;
                            border: 3px solid $border-color;
                            border-radius: 0 0 20px 20px;
                            border-top: none;

                            &.right {
                                left: auto;
                                right: -3px;
                            }
                        }
                    }


                }
            }
        }

        .title {
            position: relative;
            display: inline-block;
            padding-bottom: 16px;
            @include h2;
            @include champagneText;

            @include breakpoint(untilTablet) {
                margin-bottom: 20px;
                font-size: 28px;
                line-height: 38px;
            }

            &::after {
                height: 2px;
                width: 109px;
                content: '';
                background: $gold;
                position: absolute;
                bottom: 0;
                left: 0
            }
        }

        .text {
            @include body1;
            margin-bottom: 36px;
            color: $lightgrey;
            text-align: justify;
        }

        .view {
            display: flex;
            align-items: center;
            text-transform: uppercase;
            @include goldText;
            @include button;

            img {
                margin-left: 8px;
            }
        }
    }
}

.amenities {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    color: $lightgrey;
    height: 70vh;
    padding: 32px 0;

    @include breakpoint(desktop) {
        height: 90vh;
        padding: 60px 0;
    }

    &::after {
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.9) 100%);
        bottom: 0;
        left: 0;
        width: 100%;
        height: 50%;
        content: '';
        position: absolute;
        z-index: 1;
    }

    .bg {
        @include bg;
    }

    .row {
        z-index: 2;
        position: relative;
    }

    .title {
        @include h2;
        margin-bottom: 1rem;
    }

    .text {
        @include body1;
        text-align: justify;
    }
}

#tour {
    position: relative;
    color: $lightgrey;
    padding-top: 62px;

    .bg {
        @include bg;
    }

    .tour-img {
        img {
            width: 100%;
        }
    }

    .tour-text {
        padding-left: 2rem;

        @include breakpoint(desktop) {
            padding-left: 4rem;
        }

        @include breakpoint(untilTablet) {
            padding: 3rem 1rem;
        }
    }

    .row {
        align-items: center
    }

    .title {
        @include h2;
        @include champagneText;
        margin-bottom: 42px;
    }
}

.contact {
    position: relative;
    background-color: $lightgrey;
    padding: 64px 0;

    .container {
        position: relative;
        z-index: 2;
    }

    @include breakpoint(desktop) {
        padding: 100px 0;

        .contact-text {
            width: 70%;
        }
    }

    .graphics {
        left: 0;
        position: absolute;
        width: 100%;
        z-index: 0;
        bottom: 0;
        z-index: 1;

        img {
            width: 100%;
        }

        @include breakpoint(untilTablet) {

            img {
                width: 250%;
                object-fit: cover;
            }
        }


    }

    .subtitle {
        @include subtitle1;
        color: $primary;
        margin-bottom: 12px;
    }

    .title {
        @include h2;
        margin-bottom: 23px;
    }

    .text {
        @include body1;
        text-align: justify;
    }

    .email {
        @include breakpoint(untilLaptop) {
            margin-top: 1.5rem;
        }

        .title {
            @include subtitle1;
            color: $primary;
            margin-bottom: 24px;
        }

        p {
            margin: 0;
        }

        .btn {
            padding: 10px 14px;
        }

        .form-input {
            padding: 13px 0;
            border: none;
            border-bottom: 1px solid $primary;
            background-color: transparent;
            margin-bottom: 25px;
            width: 100%;

            &::-webkit-input-placeholder {
                text-transform: uppercase;
                color: $text;
                @include headButton;
                opacity: 0.5;
            }

            &:-ms-input-placeholder {
                color: $text;
                text-transform: uppercase;
                opacity: 0.5;
                @include headButton;
            }

            &::placeholder {
                text-transform: uppercase;
                color: $text;
                opacity: 0.5;
                @include headButton;
            }
        }
    }
}

.container-404 {
    min-height: 50vh;
    background-color: $blue;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.error404 {

    h1 {
        @include h2;
        @include champagneText;
    }

    p {
        @include body1;
        color: $lightgrey;
    }

    .back {
        @include champagneText;
        @include subtitle1;
    }
}